import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import DescriptionAlerts from './AlertComponent';

const Alert = ({ alerts }) =>
  alerts !== null &&
  alerts.length > 0 && (
    <DescriptionAlerts
      type={alerts[alerts.length - 1].alertType}
      msg={alerts[alerts.length - 1].msg}
    />
  );
Alert.prpTypes = {
  alerts: PropTypes.array.isRequired,
};
const mapStateToProps = (state) => ({
  alerts: state.alert,
});
export default connect(mapStateToProps)(Alert);
